<template>
  <div class="main-hader">
    <div class="Header">
      <router-link class="no-border" id="goTo" to="/">
        <img src="../../assets/Group 2.svg" alt="" />
      </router-link>
      <hr />
      <div class="nav">
        <router-link :key="$route.fullPath" id="goTo" to="/"><span>00</span>Home</router-link>
        <router-link id="goTo" to="/destination"
          ><span>01</span>Destination</router-link
        >
        <router-link id="goTo" to="/crew"> <span>02</span>Crew</router-link>
        <router-link id="goTo" to="/technology"
          ><span>03</span>Technology
        </router-link>
      </div>
      <div id="humb" class="humbMenu">
        <div></div>
        <div></div>
        <div></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "AppHeader",
  mounted() {
    let humbButton = document.getElementById("humb");
    let menus = document.querySelector(".nav");
    let goto = document.querySelectorAll("#goTo");

    humbButton.addEventListener("click", function () {
      if (menus.classList == "nav") {
        menus.classList.add("nav-open");
        humbButton.classList.add("humbMenu-open");
      } else {
        menus.classList.add("nav");
        menus.classList.remove("nav-open");
        humbButton.classList.remove("humbMenu-open");
      }
    });
    goto.forEach((ele) => {
      ele.addEventListener("click", function () {
        menus.classList.add("nav");
        menus.classList.remove("nav-open");
        humbButton.classList.remove("humbMenu-open");
      });
    });
  },
};
</script>

